import { ref, computed } from "vue";
import { useStore } from "vuex";
import { ArrowDown } from "@element-plus/icons-vue";
import FileSelector from "../FileSelector";
import { KML } from "ol/format";
import KMZ from "../../tools/KMZ";
import { VectorImage } from "ol/layer";
import { Vector as VectorSource } from "ol/source";
export default {
  name: "LayerPanel",
  components: {
    FileSelector
  },
  setup() {
    const store = useStore();
    const map = computed(() => store.state.map.olMap).value;
    let isShow = ref(false);
    function toggle() {
      isShow.value = !isShow.value;
    }
    function getFile(f) {
      let file = f[0];
      let reader = new FileReader();
      if (file.name.search(/\.kml$/i) !== -1) {
        reader.readAsText(file);
        reader.onload = function () {
          let data = reader.result;
          let vectorSource = new VectorSource({
            format: new KML(),
            loader: function () {
              let feats = this.getFormat().readFeatures(data);
              for (let feat of feats) {
                feat.getGeometry().transform("EPSG:4326", "EPSG:3857");
              }
              this.addFeatures(feats);
              map.getView().fit(this.getExtent());
            }
          });
          let lyr = new VectorImage({
            source: vectorSource
          });
          map.addLayer(lyr);
        };
      } else if (file.name.search(/\.kmz$/i) !== -1) {
        reader.readAsArrayBuffer(file);
        reader.onload = function () {
          let data = reader.result;
          let vectorSource = new VectorSource({
            format: new KMZ(),
            loader: function () {
              let feats = this.getFormat().readFeatures(data);
              for (let feat of feats) {
                feat.getGeometry().transform("EPSG:4326", "EPSG:3857");
              }
              this.addFeatures(feats);
              map.getView().fit(this.getExtent());
            }
          });
          let lyr = new VectorImage({
            source: vectorSource
          });
          map.addLayer(lyr);
        };
      }
    }
    return {
      isShow,
      toggle,
      getFile
    };
  }
};