import { ref } from "vue";
export default {
  props: {
    ifMultiple: Boolean,
    fileTypes: String
  },
  emits: ["getFiles"],
  setup(props, context) {
    let inp = ref();
    function openFileSelector() {
      inp.value.click();
    }
    function inpChange(e) {
      let files = e.srcElement.files;
      context.emit("getFiles", files);
    }
    return {
      inp,
      openFileSelector,
      inpChange
    };
  }
};